import React,{ Component }  from 'react';
import '../../../App.css';
import web3 from "../../../ABI/web3";
import { Table, Card ,Button,Input,Popover, Divider , Select} from 'antd';
import { GiftTwoTone, InfoCircleTwoTone} from '@ant-design/icons';
import Hongbao from '../../../ABI/hongbao';
import erc20abi from '../../../ABI/ERC20.abi.json';
const { Option } = Select;

class HBgive extends Component {
      state = {
        HBaddr:"0x49E7b8e411FAbDf44e533BBa6C74FB3Ad1D17e30",  //红包机合约地址
        HBtokenAdr:'',
        approveTotal:'',
        HBtotal:'',
        HBamount:'',
        HBremark:'',
        HBremark2:'',
        HBlownBalance:'0',
        HBpassword:'0',
        HBid:'',
        _HTforFee:'', //用HT付手续费
        HTforFee:'0',

        message:"",
        message1:'',
        message2:'',
        message3:'',

        UserName:'',
      };

  async componentDidMount(){

    if(typeof window.ethereum !== 'undefined'){
      const ethereum = window.ethereum
      //禁止页面自动刷新，metamask要求写的
      ethereum.autoRefreshOnNetwoorkChange = false
      try{
        //第一次链接Metamask
        const accounts = await ethereum.enable()
        //初始化Provider
        const provider = window['ethereum']
        if(provider.chainId == 0x1){this.setState({chainId:"以太链"});
          this.setState({disabled:"disabled"})};
        //获取用户帐号
        window.defaultAccount = accounts[0].toLowerCase()
        //切换Metamask帐号
        ethereum.on('accountsChanged', function (accounts){
          console.log("accountsChanged:" + accounts)
        })
        //切换Metamaskm网络ID
        ethereum.on(function (networkVersion){
          console.log(networkVersion)
        })
  
      }catch(e){}
    }else{console.log('没有metamask')}

    //获取用HT付发红包费用
    const _HTforFee = await Hongbao.methods.HTforFee().call();
    this.setState({_HTforFee});

  }

  onSubmitApprove = async () =>{
    const ERC20 = new web3.eth.Contract(erc20abi.abi,this.state.HBtokenAdr);
    this.setState({message1:"等待打包……"});
    await ERC20.methods.approve(this.state.HBaddr,web3.utils.toWei(this.state.approveTotal,'ether'))
    .send({from:window.defaultAccount});
    this.setState({message1:"授权成功可以进行下一步操作"});
  }

  changUserName = async () =>{
    this.setState({message3:"等待打包……"});
    await Hongbao.methods.changUserName(this.state.UserName).send({from:window.defaultAccount});
    this.setState({message3:"昵称添加成功可以进行下一步操作"});
  }

  giveHB = async () =>{
    const tokenIsPayFee = await Hongbao.methods.tokenIsPayFee(this.state.HBtokenAdr).call();
    if(tokenIsPayFee == 1){this.setState({HTforFee:this.state._HTforFee})};
    this.setState({message:"等待打包……"});
    await Hongbao.methods.giveTokenHB(
        this.state.HBtokenAdr,
        web3.utils.toWei(this.state.HBtotal,'ether'),
        this.state.HBamount,
        this.state.HBremark,
        web3.utils.toWei(this.state.HBlownBalance,'ether'),
        this.state.HBpassword
        )
    .send({from:window.defaultAccount,value:this.state.HTforFee});
    this.setState({message:"红包发送成功，快通知您的小伙伴来抢吧"});
  }

  getHBbalance =async () => {
    await Hongbao.methods.getHBbalance(this.state.HBid).send({from:window.defaultAccount});
  }

  modifyremark =async () => {
    await Hongbao.methods.modifyremark(this.state.HBid, this.state.HBremark2).send({from:window.defaultAccount});
  }

  getHBpassword =async () => {
    const passwordmessage = await Hongbao.methods.getHBpassword(this.state.HBid).call({from:window.defaultAccount});
    this.setState({message2:'密码：'+passwordmessage});
  }

  getHBlownBalance =async () => {
    const HBlownBalance = await Hongbao.methods.getHBlownBalance(this.state.HBid).call({from:window.defaultAccount});

    this.setState({message2:'大于或等于：'+ parseFloat(web3.utils.fromWei(HBlownBalance,"ether")).toFixed(2)});
  }

  handleChange =(value) =>{
    console.log(`${value}`);
     this.setState({HBtokenAdr:`${value}`});
  }
 
  render(){
      const content2 = (
        <div>
          如果有条件，留言时要注明名称、有密，然后再加上你的个性留言（如：&gt;100LG密,快来抢哦）
        </div>
      );

      const content3 = (
        <div>
          如果要设定密码，请填写数字密码，只可以填写数字。不设密码默认0即可。
          然后将密码通过微信等其它社交软件发送给您的伙伴。
          （注意：密码非绝对安全，由于区块链的透明机制，技术极客是可以破译您的密码的，
          当然大部分人是没有能力破译的，所以不可以用来发送大额红包）
        </div>
      );

      const content4 = (
        <div>
          设定领取条件，即最少拥有多少该种通证。比如输100，领取者必须拥有100该通证。默认0为无条件。
        </div>
      );

      const content5 = (
        <div>
          <p>授权给红包机多少通证额度，大于或等于您要发的红包总额。</p>
          <p>可以一次性授权大一点的额度，以免每次都要授权。</p>
          <p>比如您要发100，就授权100，也可以授权1000，这样就可以发10次100的红包。</p>
          <p>市面上通常的做法是授权无限大，链改网为了更安全故由用户自定额度。</p>
        </div>
      );

      const content6 = (
        <div>
          如果红包超过48小时未领完，发红包者可以领回剩余通证。
        </div>
      );

      const content7 = (
        <div>
          如果您设了红包密码，却又忘记了密码，则可以在此查询。
        </div>
      );
      const content8 = (
        <div>
          左边为选择栏，如果左边没有您要的通证，请在右边粘贴您的通证地址。
          只要您发过一次之后，我们就会在左边收录。
        </div>
      );
      const content9 = (
        <div>
          如果您设了条件，却又忘记了，则可以在此查询。
        </div>
      );
      const content10 = (
        <div>
          如果您忘了注明条件、有密等信息，可在下方重新输入留言，然后发送留言。
        </div>
      );
      const content11 = (
        <div>
          可以给自己填一个昵称，用于显示在红包列表。如要修改昵称，则重新发送即可。
        </div>
      );
      const content12 = (
        <div>
          指您想发出去的总共通证数量是（必填）
        </div>
      );
      const content13 = (
        <div>
          指您想包成多少个红包（必填）
        </div>
      );

      const children = [];
      children.push(
        <Option key={'0x7cc0c1a34b1ae55f9bd2a19ece9d4b166b66e70c'}>LG</Option>,
        <Option key={'0x158c9af12137cF8dfd86b3bD191DbAB79D41C856'}>YHT</Option>,
        <Option key={'0x9ef1918a9bee17054b35108bd3e2665e2af1bb1b'}>TPT</Option>,
        <Option key={'0x25d2e80cb6b86881fd7e07dd263fb79f4abe033c'}>MDX</Option>,
        <Option key={'0xa71edc38d189767582c38a3145b5873052c3e47a'}>USDT</Option>,
        <Option key={'0x5545153ccfca01fbd7dd11c0b23ba694d9509a6f'}>WHT</Option>,
        <Option key={'0x568296bBB3a64Ef6fDE6a1838d94972433f7Ee06'}>JFT</Option>,
        <Option key={'0x66a79d23e58475d2738179ca52cd0b41d73f0bea'}>HBTC</Option>,
      );


  return (
    <div>
        <Card size='small'>
            <div >昵称：<Popover content={content11} trigger="click">
                        <InfoCircleTwoTone /> 
                        </Popover>&nbsp;&nbsp;
                  <Input style={{ width: '40%' }}   value={this.state.UserName} placeholder="输入昵称" 
                        onChange={event=>{this.setState({UserName:event.target.value})}}/>
                  <Button  disabled={this.state.disabled} shape='round' type="primary"  size='middle' onClick={() => {this.changUserName()}}>发送</Button>
            </div>
            <div style={{color:"green",fontSize:"10px",textAlign:'center'}}>
                {this.state.message3}
            </div>
        </Card>


        <Card type="inner" size='small'  >
        
                <div>通证：<Popover content={content8} trigger="click">
                                <InfoCircleTwoTone /> 
                                </Popover>&nbsp;&nbsp;
                      <Select   style={{ width: '25%' }}   onChange={this.handleChange}>{children}</Select>
                      <Input  style={{ width: '45%' }} value={this.state.HBtokenAdr} placeholder="输入通证地址" 
                      onChange={event=>{this.setState({HBtokenAdr:event.target.value})}}/>
                </div>

                
                    <div style={{marginTop:'5px'}}>授权：
                          <Popover content={content5} trigger="click">
                                <InfoCircleTwoTone /> 
                          </Popover>&nbsp;&nbsp;
                          <Input  style={{ width: '40%' }} value={this.state.approveTotal} placeholder="输入授权数量" 
                                onChange={event=>{this.setState({approveTotal:event.target.value})}}/>
                          <Button  disabled={this.state.disabled} shape='round' type="primary"  size='middle' onClick={() => {this.onSubmitApprove()}}>授权</Button>
                    </div>
                    <div style={{color:"green",fontSize:"10px",textAlign:'center'}}>
                        {this.state.message1}
                    </div>
                
                <Divider/>

                <div style={{marginTop:'5px'}}>总额：
                      <Popover content={content12} trigger="click">
                            <InfoCircleTwoTone /> 
                      </Popover>&nbsp;&nbsp;
                      <Input bordered={false} style={{ width: '78%' }} value={this.state.HBtotal} placeholder="红包中通证总数量" 
                      onChange={event=>{this.setState({HBtotal:event.target.value})}}/>
                </div>

                <div style={{marginTop:'5px'}}>个数：
                      <Popover content={content13} trigger="click">
                            <InfoCircleTwoTone /> 
                      </Popover>&nbsp;&nbsp;
                      <Input bordered={false} style={{ width: '78%' }} value={this.state.HBamount} placeholder="红包个数" 
                      onChange={event=>{this.setState({HBamount:event.target.value})}}/>
                </div>

                <div style={{marginTop:'5px'}}>留言：
                      <Popover content={content2} trigger="click">
                            <InfoCircleTwoTone /> 
                      </Popover>&nbsp;&nbsp;
                      <Input bordered={false} style={{ width: '71%' }} value={this.state.HBremark} placeholder="填写个性留言" 
                      onChange={event=>{this.setState({HBremark:event.target.value})}}/>
                </div>

                <div style={{marginTop:'5px'}}>条件：<Popover content={content4} trigger="click">
                          <InfoCircleTwoTone /> 
                          </Popover>&nbsp;&nbsp;
                    <Input bordered={false}  style={{ width: '71%' }} placeholder=">=多少,无条件0" value={this.state.HBlownBalance}
                        onChange={event=>{this.setState({HBlownBalance:event.target.value})}} />
                </div>

                <div style={{marginTop:'5px'}}>密码：<Popover content={content3} trigger="click"> 
                          <InfoCircleTwoTone /> 
                          </Popover>&nbsp;&nbsp;
                <Input bordered={false} style={{ width: '71%' }} placeholder="红包密码,无密输0" value={this.state.HBpassword}
                    onChange={event=>{this.setState({HBpassword:event.target.value})}} />
                </div>
            
              <Button  shape='round' block type="primary" danger size="large" style={{marginTop:'6px'}} 
                      onClick={() => {this.giveHB()}}>发红包</Button>

              <div style={{color:"green",fontSize:"13px",textAlign:'center'}}>
                        {this.state.message}
              </div>
         </Card>

         <Card size='small' >
                <Input  value={this.state.HBid} placeholder="输入红包ID" 
                            onChange={event=>{this.setState({HBid:event.target.value})}}/>
                <div style={{marginTop:'5px'}}>领回：
                    <Popover content={content6} trigger="click">
                          <InfoCircleTwoTone /> 
                    </Popover>&nbsp;&nbsp;&nbsp;
                    <Button style={{width:'100px'}} shape='round' onClick={() => {this.getHBbalance()}}  type="primary"  size='middle'>领回</Button>
                </div>

                <div style={{marginTop:'5px'}} >查密：
                    <Popover content={content7} trigger="click">
                          <InfoCircleTwoTone /> 
                    </Popover>&nbsp;&nbsp;&nbsp;
                    <Button style={{width:'100px'}} shape='round' onClick={() => {this.getHBpassword()}}  type="primary"  size='middle'>查密码</Button>
                </div>

                <div style={{marginTop:'5px'}} >条件：
                      <Popover content={content9} trigger="click">
                            <InfoCircleTwoTone /> 
                      </Popover>&nbsp;&nbsp;&nbsp;
                      <Button style={{width:'100px'}} shape='round' onClick={() => {this.getHBlownBalance()}}  type="primary"  size='middle'>查条件</Button>
                </div>

                <div style={{color:"green",fontSize:"15px",textAlign:'center'}}>
                            {this.state.message2}
                </div>

                <div style={{marginTop:'5px'}}>留言：
                      <Popover content={content10} trigger="click">
                            <InfoCircleTwoTone /> 
                      </Popover>&nbsp;&nbsp;&nbsp;
                      <Button style={{width:'100px'}} shape='round' onClick={() => {this.modifyremark()}}  type="primary"  size='middle'>留言</Button>
                </div>
                <Input  value={this.state.HBremark2} placeholder="填写个性留言" 
                          onChange={event=>{this.setState({HBremark2:event.target.value})}}  />
        </Card>
    </div>
  );
}
}

export default HBgive;
