import React,{ Component }  from 'react';
import { Collapse } from 'antd';
import '../../App.css';
import { Card } from 'antd';
import { Link } from 'react-router-dom';
const { Panel } = Collapse;

const text1 = `
  任何人都可以通过本工具向您的小伙伴们空投您喜欢的任何火币链上的通证、 且可以多次发布。
  `;
const text2 = `
    重磅来袭！欢迎体验！发红包即可挖矿LG哦！
  `;
const text3 = `
  敬请期待……
  `;

function callback(key) {
  console.log(key);
}

class mainCardShentai extends Component {
 
  render(){
    
  return (
              <Card type="inner" title="生态" >
                  <Collapse  onChange={callback} style={{background:'white'}}>
                      <Panel header="天女号空投机" key="1" extra={<Link to="/STAirdrop">进入</Link>}>
                        <p>{text1}</p>
                      </Panel>
                      <Panel header="发红包" key="2" extra={<Link to="/hongbao">进入</Link>}>
                        <p>{text2}</p>
                       
                      </Panel>
                      <Panel header="更多……" key="3" >
                        <p>{text3}</p>
                      </Panel>
                  </Collapse>
              </Card>
          
  );
}
}

export default mainCardShentai;
