import React,{ Component }  from 'react';
import '../../App.css';
import web3 from "../../ABI/web3";
import { Card ,Button,Input,Popover, Divider} from 'antd';
import { InfoCircleTwoTone, QuestionCircleOutlined} from '@ant-design/icons';
import HangQin from '../../ABI/hangqin';
import erc20abi from '../../ABI/ERC20.abi.json';

class Hangqinadd extends Component {
      state = {
        tokenAdr:'',
        tokenPairAdr:'',

        tokenId:'',
        newtokenAdr:'',
        newtokenPairAdr:'',

      };

  async componentDidMount(){

    if(typeof window.ethereum !== 'undefined'){
      const ethereum = window.ethereum
      //禁止页面自动刷新，metamask要求写的
      ethereum.autoRefreshOnNetwoorkChange = false
      try{
        //第一次链接Metamask
        const accounts = await ethereum.enable()
        //初始化Provider
        const provider = window['ethereum']
        if(provider.chainId == 0x1){this.setState({chainId:"以太链"});
          this.setState({disabled:"disabled"})};
        //获取用户帐号
        window.defaultAccount = accounts[0].toLowerCase()
        //切换Metamask帐号
        ethereum.on('accountsChanged', function (accounts){
          console.log("accountsChanged:" + accounts)
        })
        //切换Metamaskm网络ID
        ethereum.on(function (networkVersion){
          console.log(networkVersion)
        })
  
      }catch(e){}
    }else{console.log('没有metamask')}

  }



  addToken = async () =>{
    const ERC20 = new web3.eth.Contract(erc20abi.abi,this.state.tokenAdr);
    const symbol = await ERC20.methods.symbol().call();
    const decimals = await ERC20.methods.decimals().call();
    await HangQin.methods.addToken(
        this.state.tokenAdr,
        this.state.tokenPairAdr,
        symbol,
        decimals
        )
    .send({from:window.defaultAccount});
  }

  userModifyToken = async () =>{
    const ERC20 = new web3.eth.Contract(erc20abi.abi,this.state.newtokenAdr);
    const symbol = await ERC20.methods.symbol().call();
    const decimals = await ERC20.methods.decimals().call();
    await HangQin.methods.userModifyToken(
        this.state.tokenId,
        this.state.newtokenAdr,
        this.state.newtokenPairAdr,
        symbol,
        decimals
        )
    .send({from:window.defaultAccount});
  }


  render(){

      const content1 = (
        <div>
          通证的合约地址
        </div>
      );
      const content2 = (
        <div>
          <p>注意：只支持HT与该通证的交易对</p>
          <p>通证的交易对地址，即该通证在去中心化交易所交易的地址</p>
        </div>
      );
      const content3 = (
        <div>
          <p>如果不能正常显示价格请在下方修改</p>
          <p>不能正常显示的原因主要以下两点:</p>
          <div>1. 通证地址或交易对地址错误</div>
          <div>2. 非HT交易对，即该地址下的交易对不是HT与该通证</div>
        </div>
      );
      const content4 = (
        <div>
          <p>在行情页上查看您刚刚上传的该通证对应的ID</p>
          <p>ID不对无法修改，您只可以修改您发布的通证</p>
        </div>
      );

  return (
    <div>
        <Card type="inner" size='small'  >
                <div style={{marginTop:'5px'}}>通证地址：
                      <Popover content={content1} trigger="click">
                            <InfoCircleTwoTone /> 
                      </Popover>
                      </div>
                      <Input   value={this.state.tokenAdr} placeholder="通证地址" 
                      onChange={event=>{this.setState({tokenAdr:event.target.value})}}/>
                

                <div style={{marginTop:'5px'}}>交易地址：
                      <Popover content={content2} trigger="click">
                            <InfoCircleTwoTone /> 
                      </Popover>
                </div>
                <Input  value={this.state.tokenPairAdr} placeholder="与HT兑换的交易对地址" 
                      onChange={event=>{this.setState({tokenPairAdr:event.target.value})}}/>
                <Button  shape='round' block type="primary"  size="middle" style={{marginTop:'6px'}} 
                      onClick={() => {this.addToken()}}>发送</Button>
         
         <Divider>修改
                <Popover content={content3} trigger="click">
                   <QuestionCircleOutlined /> 
                </Popover>
         </Divider>

                <div style={{marginTop:'5px'}}>通证ID：
                    <Popover content={content4} trigger="click">
                      <InfoCircleTwoTone /> 
                    </Popover>
                </div>
                      <Input  value={this.state.tokenId} placeholder="在行情页上查看该通证的ID" 
                      onChange={event=>{this.setState({tokenId:event.target.value})}}/>
                
         
                <div style={{marginTop:'5px'}}>通证地址：
                      <Popover content={content1} trigger="click">
                            <InfoCircleTwoTone /> 
                      </Popover>
                </div>
                <Input   value={this.state.newtokenAdr} placeholder="通证地址" 
                      onChange={event=>{this.setState({newtokenAdr:event.target.value})}}/>
                

                <div style={{marginTop:'5px'}}>交易地址：
                      <Popover content={content2} trigger="click">
                            <InfoCircleTwoTone /> 
                      </Popover>
                </div>
                <Input  value={this.state.newtokenPairAdr} placeholder="与HT兑换的交易对地址" 
                      onChange={event=>{this.setState({newtokenPairAdr:event.target.value})}}/>
  
              <Button  shape='round' block type="primary"  size="middle" style={{marginTop:'6px'}} 
                      onClick={() => {this.userModifyToken()}}>发送</Button>
         </Card>
    </div>
  );
}
}

export default Hangqinadd;
