import React,{ Component }  from 'react';
import '../../../App.css';
import web3 from "../../../ABI/web3";
import { Link } from 'react-router-dom';
import { Table, Card ,Button,Input,Popover, Result} from 'antd';
import { GiftTwoTone, QuestionCircleOutlined} from '@ant-design/icons';
import Hongbao from '../../../ABI/hongbao';
import erc20abi from '../../../ABI/ERC20.abi.json';



class HBhome extends Component {
      state = {
        requests:[],
        HBid:'',
        HBpassword:'0',
        onoff:'none',
        onoff2:'none',
        lastHB:'',
        lastHBsymbol:'',
        message:"",
        
        
      };

  async componentDidMount(){


    if(typeof window.ethereum !== 'undefined'){
      const ethereum = window.ethereum
      //禁止页面自动刷新，metamask要求写的
      ethereum.autoRefreshOnNetwoorkChange = false
      try{
        //第一次链接Metamask
        const accounts = await ethereum.enable()
    
        //初始化Provider
        const provider = window['ethereum']
        
        if(provider.chainId == 0x1){this.setState({chainId:"以太链"});
          this.setState({disabled:"disabled"})};
  
        //获取用户帐号
        window.defaultAccount = accounts[0].toLowerCase()

        //切换Metamask帐号
        ethereum.on('accountsChanged', function (accounts){
          console.log("accountsChanged:" + accounts)
        })
  
        //切换Metamaskm网络ID
        ethereum.on(function (networkVersion){
          console.log(networkVersion)
        })
  
      }catch(e){}
    }else{console.log('没有metamask')}

    
    // this.setState({HBtotal});

    
  


  //   const ID = [];
  //   const remark = [];
  //   const amount = [];
  //   for(var i=1; i<=HBtotal; i++ ){
  //   const HBinfo = await Hongbao.methods.getHBinfo(i).call();
  //   ID.push(HBinfo[0]);
  //   remark.push(HBinfo[2]);
  //   amount.push(HBinfo[5]);
  // }
  const HBtotal = await Hongbao.methods.HBid().call();

    const dataSource2 = []
    for(var i=0; i<=HBtotal; i++ ){
      const HBinfo2 = await Hongbao.methods.getHBinfo(i).call({from:window.defaultAccount});
      HBinfo2[6] = parseFloat(web3.utils.fromWei(HBinfo2[6],"ether")).toFixed(1);
      if(HBinfo2[5] !=0){
      dataSource2.push(HBinfo2);
      }
    }
      console.log(dataSource2);
      
    // const requests = await Promise.all(
    //   Array(5).fill().map((element,index)=>{
    //    return Hongbao.methods.getHBinfo(index).call();
    //   })
    // )


    // console.log(requests);
      this.setState({requests:dataSource2});

  // this.setState({ID:ID});this.setState({remark:remark});this.setState({amount:amount});


  }


  getHB =async () => {
    this.setState({message:"等待打包……"});
    await Hongbao.methods.getHB(this.state.HBid, this.state.HBpassword).send({from:window.defaultAccount});
    this.setState({message:"领取成功请点击'查询结果'"});
  }

  open = async () => {

    const lastHBadr = await Hongbao.methods.lastTokenAdr(window.defaultAccount).call();
    const ERC20 = new web3.eth.Contract(erc20abi.abi,lastHBadr);
    const lastHBsymbol = await ERC20.methods.symbol().call();
    this.setState({lastHBsymbol:lastHBsymbol});

    const lastHB = await Hongbao.methods.lastHB(window.defaultAccount).call();
    if(lastHBsymbol == 'HBTC'){
    const _lastHB = ((parseFloat(web3.utils.fromWei(lastHB,'Gwei')))/10).toFixed(2);
    this.setState({lastHB:_lastHB});
    this.setState({lastHBsymbol:'聪BTC'});
    }else{
      const _lastHB = parseFloat(web3.utils.fromWei(lastHB,"ether")).toFixed(2);
    
      this.setState({lastHB:_lastHB});
    }



    
    this.setState({onoff:"block"});
  }
  
  close = () => {
    this.setState({onoff:"none"});
  }

  open2 = () => {
    this.setState({onoff2:"block"});
  }

  close2 = () => {
    this.setState({onoff2:"none"});
  }

  handleChange =(value) =>{
    console.log(`${value}`);
     this.setState({HBid:`${value}`});
  }
 
  render(){

    const columns = [
      {
        title: 'ID',
        dataIndex: '0',
        key:'0',
      },
      {
        title: 'who',
        dataIndex: '1',
      },
      {
        title: '留言',
        dataIndex: '2',
      },
      {
        title: '余额',
        dataIndex: '6',
      },
      {
        title: '剩',
        dataIndex: '5',
      }
    ];

    const LastHB = []
      LastHB.push(this.state.lastHB, this.state.lastHBsymbol)

      const content = (
        <div>
          <p>1. 手续费及挖矿比率可能会适时调整</p>
          <p>2. 发红包者可以在首页贡献值内领取LG矿，不用每发一次红包就领一次，可以一个月领一次</p>
          <p>3. 手续费收益的10~50%将用于回购LG或添加LG流动池</p>
        </div>
      );


  return (
    <div>
    <Card  type="inner" size='small' title="红包(持有100LG,是抢/发红包的前提条件)" >
      
      <Table size='small' columns={columns}  dataSource={this.state.requests} bordered />

      <div>
        <span>输入红包ID</span><span style={{float:'right'}}>输入红包密码(无密为0)</span>
        
      </div>
      <Input style={{ width: '50%' }} placeholder="输入红包ID" value={this.state.HBid}
          onChange={event=>{this.setState({HBid:event.target.value})}}
      />
      <Input style={{ width: '50%' }} placeholder="输入红包密码" value={this.state.HBpassword}
          onChange={event=>{this.setState({HBpassword:event.target.value})}}
      />
      <br/>
        <Button  shape='round' block type="primary" danger size="large" style={{marginTop:'6px'}} onClick={() => {this.getHB()}}>抢红包</Button>
      <div style={{color:"green",fontSize:"13px",textAlign:'center'}}>
                {this.state.message}
      </div>

      <Button shape='round' type="primary" block color='green' size="middle" style={{marginTop:'6px'}} 
        onClick={() => {this.open()}}>查询结果</Button>

      <Result style={{display:this.state.onoff}} 
        icon={<GiftTwoTone />}
        title={LastHB}
        extra={<Button shape='round' type="primary" onClick={() => {this.close()}}>关闭</Button>}
      />

      <Button  shape='round' type="primary" block color='green' size="middle" style={{marginTop:'6px'}} >
          <Link to="/HBgive">发红包</Link>
      </Button>
    </Card>

    
    <Card type="inner" size='small'  >
    <Button shape='round' type="dashed" block  size="small"  onClick={() => {this.open2()}}>说明</Button>
      <div style={{display:this.state.onoff2}}>
          <div>1. 持用100或以上LG，是发、领红包的前提条件</div>
          <div>2. 一个帐号只可领取一次</div>
          <div>3. 红包总额5%挖矿LG由发红包者获得，5%为手续费用于回馈所有LG成员
                <Popover content={content} title="说明" trigger="click">
                    <QuestionCircleOutlined />
                </Popover>
          </div>
          <Button shape='round' block type="dashed" size="small" onClick={() => {this.close2()}}>关闭</Button>
      </div>
    </Card>
    </div>
  );
}
}

export default HBhome;
