import React, { Component } from 'react';
import '../../App.css';
import web3 from "../../ABI/web3";
import anyAirdrop from '../../ABI/anyAirdrop';
import GXzhi from '../../ABI/GxzhiABI';
import { Select,Input,Button,Divider,Card } from 'antd';
const { Option } = Select;




class STAirdrop extends Component {

   state = {
    AirdropAdr:"",  //YHT默认地址
    AirdropAmount:"",
    AirdropTotal:"",
    ApproveTotal:"",
    anyAirdropaddr:"0x68eDC077a12980525442e7B4e781951CFdF60701",  //空投机合约地址
    erc20abi:"",
    AirdropinfoAmount:"",
    ERC20symbol:"",
    AirdropBalance:"",
    modifyAirdropAmount:"",
    GxianID:"",
    message:"在上方输入通证的合约地址再点领取",
    onoff2:'none',

   };

async componentDidMount(){
  //判断页面是否安装了Metamask
  if(typeof window.ethereum !== 'undefined'){
    const ethereum = window.ethereum
    //禁止页面自动刷新，metamask要求写的
    ethereum.autoRefreshOnNetwoorkChange = false
    try{
      //第一次链接Metamask
      const accounts = await ethereum.enable()
      console.log(accounts)
      //初始化Provider
      const provider = window['ethereum']
      console.log(provider)
      //获取网络ID
      console.log(provider.chainId)
      //获取用户帐号
      window.defaultAccount = accounts[0].toLowerCase()
      console.log(window.defaultAccount)
      //切换Metamask帐号
      ethereum.on('accountsChanged', function (accounts){
        console.log("accountsChanged:" + accounts)
      })
      //切换Metamaskm网络ID
      ethereum.on(function (networkVersion){
        console.log(networkVersion)
      })
    }catch(e){
    }
  }else{console.log('没有metamask')}


  const erc20abi = require("../../ABI/ERC20.abi.json");
  this.setState({erc20abi});

  //获取贡献人数
  const GxianID = await GXzhi.methods.userID().call();
  this.setState({GxianID});

}



onSubmitApprove = async event =>{
  event.preventDefault();//取消表单的默认提交
  const ERC20 = new web3.eth.Contract(this.state.erc20abi.abi,this.state.AirdropAdr);
  ERC20.methods.approve(this.state.anyAirdropaddr,web3.utils.toWei(this.state.ApproveTotal,'ether'))
  .send({from:window.defaultAccount});
}


onSubmitgiveAirdrop = async event =>{
  event.preventDefault();//取消表单的默认提交
  anyAirdrop.methods.giveAirdrop(this.state.AirdropAdr,web3.utils.toWei(this.state.AirdropAmount,'ether'),web3.utils.toWei(this.state.AirdropTotal,'ether'))
  .send({from:window.defaultAccount});
}


onSubmitgetAirdrop = async ()  =>{
  this.setState({message:"等待打包……"});
  await anyAirdrop.methods.getAirdrop(this.state.AirdropAdr).send({from:window.defaultAccount});
  this.setState({message:"领取成功请回钱包首页查看"});
}

claim = async event =>{
  event.preventDefault();//取消表单的默认提交
  anyAirdrop.methods.claim(this.state.AirdropAdr).send({from:window.defaultAccount});
}

modifyAirdropAmount = async event =>{
  event.preventDefault();//取消表单的默认提交
  anyAirdrop.methods.modifyAirdropAmount(this.state.AirdropAdr,web3.utils.toWei(this.state.modifyAirdropAmount,'ether')).send({from:window.defaultAccount});
}

// web3.utils.fromWei(this.state.AirdropBalance,'ether')
// web3.utils.toWei(this.state.modifyAirdropAmount,'ether')

getAirdropinfo =() => {
  const ERC20 = new web3.eth.Contract(this.state.erc20abi.abi,this.state.AirdropAdr);
  ERC20.methods.symbol().call().then(ERC20symbol=>{this.setState({ERC20symbol})});
  ERC20.methods.balanceOf(this.state.anyAirdropaddr).call().then(AirdropBalance=>{this.setState({AirdropBalance:web3.utils.fromWei(AirdropBalance,'ether')})})
  anyAirdrop.methods.getAirTokenInfo(this.state.AirdropAdr).call().then(airInfo=>{
    console.log(airInfo);
    this.setState({AirdropinfoAmount:airInfo[1]/10**18});
    console.log(this.state.AirdropinfoAmount);
  })
}

 handleChange =(value) =>{
  console.log(`${value}`);
   this.setState({AirdropAdr:`${value}`});
}

open2 = () => {
  this.setState({onoff2:"block"});
}

close2 = () => {
  this.setState({onoff2:"none"});
}
  render(){

    const children = [];
      children.push(
        <Option key={'0x7cc0c1a34b1ae55f9bd2a19ece9d4b166b66e70c'}>LG</Option>,
        <Option key={'0x158c9af12137cF8dfd86b3bD191DbAB79D41C856'}>YHT</Option>,
        <Option key={'0x568296bBB3a64Ef6fDE6a1838d94972433f7Ee06'}>JFT</Option>,
        
      );




  return (

      <div >
        <Card size='small' >

          <Button shape='round' type="dashed" block  size="middle"  onClick={() => {this.open2()}}>天女号空投机简介</Button>
          <div style={{display:this.state.onoff2}}>
              <div>任何人都可以通过本工具向您的小伙伴们空投您喜欢的任何火币链上的通证、
                  且可以多次发布。
              </div>
              <div style={{color:"red",marginTop:'10px'}}>免责声明：本工具内的通证为用户所发，空投机只是一个工具，
                  不提供审核工作，请自行评估风险。
              </div>
              <div style={{textAlign:'right'}}><a href="https://hecoinfo.com/address/0x68eDC077a12980525442e7B4e781951CFdF60701#code">
                  查看空投机合约源代码
                  </a>
              </div>
              
              <Button shape='round' block type="dashed" size="middle" onClick={() => {this.close2()}}>关闭</Button>
          </div>
            
          <div>
              <Divider>领空投</Divider>
              <div>
                  <div>

                       <p>地址:
                          <Select   style={{ width: '30%' }}   onChange={this.handleChange}>
                              {children}
                            </Select>
                            <Input style={{ width: '50%' }} value={this.state.AirdropAdr} placeholder="左边选择或者在此输入" 
                            onChange={event=>{this.setState({AirdropAdr:event.target.value})}}/>
                       </p>

                      

                        <p style={{color:"blue", marginBottom:'10px'}}>
                            名称：{this.state.ERC20symbol}&nbsp;&nbsp;&nbsp;&nbsp;
                            可领：{this.state.AirdropinfoAmount}&nbsp;&nbsp;&nbsp;&nbsp;
                            余额：{this.state.AirdropBalance}&nbsp;&nbsp;&nbsp;&nbsp;
                        </p>
                  </div>

                  <sapn  >
                      <Button  style={{width:'50%'}} shape='round' type="primary"  size='large'  
                      onClick={() => {this.getAirdropinfo()}}>查询空投</Button>
                      
                  <Button danger style={{width:'50%'}} shape='round'  type="primary"  size='large' 
                  onClick={() => {this.onSubmitgetAirdrop()}}>领取空投</Button>
                  </sapn>

              </div>

          </div>
          </Card>
          <Card size='small'>

              <Divider>发送空投</Divider>
              <div>
                <form  onSubmit = {this.onSubmitApprove}>
                  <p>通证地址：
                      <Input  style={{width:"305px"}} value={this.state.AirdropAdr}
                        placeholder="输入通证的合约地址"
                        onChange={event=>{this.setState({AirdropAdr:event.target.value})}}/>
                  </p>
                  
                  <p>
                    授权数量：
                      <Input  style={{width:"120px"}} value={this.state.ApproveTotal}
                        placeholder="输入授权额"
                        onChange={event=>{this.setState({ApproveTotal:event.target.value})}}/>
                  <Button danger  shape='round' style={{float:"right"}}>授权</Button>
                  </p>
                </form>
              </div>
              
                <form  onSubmit = {this.onSubmitgiveAirdrop}>
                  <p>
                    单个数量：
                      <Input  style={{width:"120px"}} value={this.state.AirdropAmount}
                        placeholder="单个空投额度"
                        onChange={event=>{this.setState({AirdropAmount:event.target.value})}}/>
                  </p>
                  <p>
                    空投总量：
                      <Input  style={{width:"120px"}} value={this.state.AirdropTotal}
                        placeholder="不得大于授权额度"
                        onChange={event=>{this.setState({AirdropTotal:event.target.value})}}/>

                  <Button danger shape='round' block style={{float:"right", marginTop:'10px', marginBottom:'15px'}}>发送</Button>
                  </p>
                  </form>

                  <Divider />

                  <form onSubmit = {this.modifyAirdropAmount}>
                      <p>修改单个空投数量：
                        <Input  style={{width:"100px"}} value={this.state.modifyAirdropAmount} 
                        placeholder="单个空投额度"
                        onChange={event=>{this.setState({modifyAirdropAmount:event.target.value})}}/>
                      <Button danger style={{float:"right"}} shape='round' >修改</Button>
                      </p>
                  </form>
                  <Divider style={{margin:'15px'}}  />
                  <form onSubmit = {this.claim}>
                      <Button  danger shape='round' block>领取剩余的全部空投</Button>
                  </form>
          </Card>
         
      </div>
  );
  }
}


export default STAirdrop;
